

























module.exports =
    name: 'Login'
    data: ->
        account:
            username: 'yong', password: '1'
    methods:
        login: ->
            {username, password} = this.account
            console.log {username, password}
            @.$router.push 'home'
    created: ->
        @.$verify
            'account.username':
                required: true
            'account.password':
                required: true



